"use client";
import React, { useEffect, useState } from "react";
import { FiShoppingCart } from "react-icons/fi";
import { FaSun, FaMoon } from "react-icons/fa";
import { useTheme } from "next-themes";

const navLinks = [
  { name: "Home", href: "#", active: true },
  { name: "Tops", href: "#" },
  { name: "Bottoms", href: "#" },
  { name: "Accessories", href: "#" },
  { name: "Contact", href: "#" },
];

export default function Navbar() {
  const { theme, setTheme } = useTheme();
  const [mounted, setMounted] = useState(false);

  // Prevent hydration mismatch
  useEffect(() => {
    setMounted(true);
  }, []);

  return (
    <nav className="fixed top-0 left-0 w-full bg-white dark:bg-black border-b border-gray-200 dark:border-gray-700 px-8 py-3 flex items-center justify-between z-50 shadow-md">
      <div className="flex items-center space-x-2">
        <span className="text-black dark:text-white font-bold text-3xl tracking-tight">
          LUXORO
        </span>
        <span className="ml-4 text-xl text-black dark:text-white tracking-wide">
          Online Store
        </span>
      </div>
      <div className="flex items-center space-x-6">
        {navLinks.map((link) => (
          <a
            key={link.name}
            href={link.href}
            className={`text-lg font-mono tracking-wide transition-colors ${
              link.active
                ? "text-gray-400 cursor-default"
                : "text-black dark:text-white hover:text-gray-600 dark:hover:text-gray-300"
            }`}
          >
            {link.name}
          </a>
        ))}
        <FiShoppingCart className="text-2xl text-black dark:text-white hover:text-gray-600 dark:hover:text-gray-300 transition-colors cursor-pointer" />

        {/* Toggle button */}
        {mounted && (
          <button
            onClick={() => setTheme(theme === "light" ? "dark" : "light")}
            className="text-2xl transition-colors text-black dark:text-white hover:text-gray-600 dark:hover:text-gray-300"
          >
            {theme === "light" ? <FaMoon /> : <FaSun />}
          </button>
        )}
      </div>
    </nav>
  );
}